.stages__container {
  margin-bottom: 165px;
  @include mobile {
    margin-top: 83px;
    margin-bottom: 60px;
  }
}

.stages__container-title {
  width: 65%;
  margin-bottom: 3.2vmin;
  @include mobile {
    width: 100%;
    margin-bottom: 38vmin;
  }
}

.stages__title {
  display: inline;
  font-size: 3.75vmin;
  letter-spacing: .8px;
  line-height: 4.3vmin;
  @include mobile {
    display: block;
    font-size: 10.2vmin;
    line-height: 10.4vmin;
    margin-bottom: 13px;
  }
}

.stages__link {
  display: inline-block;
  width: 30%;
  color: var(--color-blue);
  transform: translateX(-10px);
  letter-spacing: 1.6px;
  line-height: 1.5vmin;
  @include small-tablet {
    width: 35%;
  }
  @include mobile {
    display: block;
    width: auto;
    font-size: 4.5vmin;
    transform: translateX(0px);
    line-height: 5.2vmin;
    letter-spacing: 1px;
  }
}

.stages__container-list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: .7rem;
  width: 87%;
  counter-reset: number 0;
  @include mobile {
    display: none;
  }
}

.stages__container-item {
  position: relative;
  padding: 21px 22px 15px 63px;
  background: url('../img/wall.jpg');
  line-height: 1.6vmin;
  letter-spacing: 1.2px;
  &::before {
    content: counter(number);
    counter-increment: number 1;
    font-weight: 600;
    position: absolute;
    top: 21px;
    left: 1.6vmin;
    padding: 4px 9px;
    border-radius: 50%;
    background-color: var(--color-white);
  }
  &:nth-child(3) {
    grid-row: span 2;
  }
  &:nth-child(7) {
    position: relative;
    grid-column: 2 / span 2;
    &::after {
      content: '';
      position: absolute;
      width: 24vmin;
      height: 13vmin;
      top: -57%;
      right: -5%;
      background: url('../img/plane.png') no-repeat;
      background-size: contain;
      @include tablet {
        width: 26vmin;
        height: 15vmin;
        top: -30%;
        right: -9%;
      }
      @include small-tablet {
        width: 25vmin;
        height: 13vmin;
        top: 0;
      }
    }
    span {
      display: block;
      width: 60%;
    }
  }
}

.stages__swiper {
  display: none;
  @include mobile {
    display: block;
    overflow: visible;
    min-height: 91vmin;
  }
}

.stages__swiper-wrapper {
  counter-reset: number 0;
}

.stages__swiper-slide {
  @include flex;
  height: auto;
  &:nth-child(1),
  &:nth-child(3) {
    flex-direction: column;
  }
}

.stages__swiper-item {
  font-size: 4.5vmin;
  position: relative;
  padding: 15.9vmin 5vmin 5vmin 18.7vmin;
  padding-bottom: 15px;
  background: url('../img/wall.jpg');
  line-height: 5.6vmin;
  letter-spacing: .8px;
  // height: auto;
  &::before {
    content: counter(number);
    @include flex-all-center;
    counter-increment: number 1;
    font-weight: 600;
    position: absolute;
    top: 16vmin;
    left: 5vmin;
    width: 9vmin;
    height: 9vmin;
    // padding: 8px 8px;
    border-radius: 50%;
    background-color: var(--color-white);
  }
  &.second {
    padding: 3.6vmin 5vmin 9vmin 18vmin;
    &::before {
      top: 3.6vmin;
    }
  }
  &.fifth {
    padding: 1.1vmin 5vmin 12vmin 15vmin;
    &::before {
      top: 2px;
    }
  }
}

.stages__swiper-options {
  position: absolute;
  left: 50%;
  bottom: 2vmin;
  width: 52%;
  transform: translateX(-50%);
}

.stages__swiper-pagination {
  bottom: 0 !important;
  width: fit-content !important;
  left: 50% !important;
  transform: translateX(-50%);
  span {
    width: 2.3vmin !important;
    height: 2.3vmin !important;
    margin: 0 1.1vmin !important;
  }
}

.stages__swiper-prev {
  left: 0;
}

.stages__swiper-next {
  right: 0;
}

.stages__swiper-image {
  position: absolute;
  top: -22vmin;
  left: 7vmin;
  height: 30vmin;
  z-index: 1;
  @include mobile {
    top: -30vmin;
    height: 42vmin;
  }
}
