.running {
  @include flex;
  white-space: nowrap;
  // gap: 1rem;
  padding: 1vmin 0;
  color: var(--color-white);
  background-color: var(--color-red);
  @include mobile {
    padding: 3vmin 0;
  }
}

.running__content {
  @include flex;
  font-size: 1.5vmin;
  animation: run 15s linear infinite;
  @include mobile {
    font-size: 3vmin;
  }
}

.running__content-text {
  @include flex-v-center;
  gap: 5px;
  margin-left: 5px;
  &::after {
    content: '';
    display: inline-block;
    width: 5px;
    height: 5px;
    background-color: var(--color-white);
    border-radius: 50%;
  }

}

@keyframes run {
  from {
      transform: translateX(0);
  }
  to {
      transform: translateX(calc(-100% - 6rem));
  }
}
