.hero__container {
  margin-top: 95px;
  margin-bottom: 130px;
  @include mobile {
    margin-top: 50px;
    margin-bottom: 25px;
  }
}

.hero__container-top {
  position: relative;
  margin-bottom: 30px;
  @include mobile {
    margin-bottom: 0;
  }
}

.hero__top-content {
  width: 55%;
  line-height: 3.15vmin;
  @include mobile {
    @include flex;
    flex-direction: column;
    width: 100%;
    line-height: 5.5vmin;
  }
}

.hero__top-title {
  display: inline;
  font-size: 2.65vmin;
  line-height: 1vmin;
  letter-spacing: -1px;
  b {
    letter-spacing: -1.2px;
    @include mobile {
      display: block;
    }
  }
  @include mobile {
    display: block;
    font-size: 7.9vmin;
    line-height: 9.5vmin;
    &:nth-child(1) {
      order: 0;
    }
    &:nth-child(2) {
      order: 1;
    }
  }
}

.hero__top-img {
  position: absolute;
  top: -2.1vmin;
  right: 11.7vmin;
  height: 16.3vmin;
  img {
    height: 100%;
  }
  @include mobile {
    position: static;
    height: auto;
    margin: 7vmin 0 4vmin;
  }
}

.hero__container-bottom {
  @include flex;
  align-items: stretch;
  gap: 2.6rem;
  @include mobile {
    flex-direction: column;
    gap: 0;
  }
}

.hero__bottom-img {
  height: 40vmin;
  margin-left: 2px;
  @include small-tablet {
    height: 60vmin;
  }
  img {
    height: 100%;
  }
  @include mobile {
    height: 112vmin;
    margin-top: 5vmin;
    margin-left: 0;
  }
}

.hero__bottom-content {
  @include flex;
  flex-direction: column;
  justify-content: space-between;
  width: 72%;
  margin: 40px 0 30px;
  @include mobile {
    margin: 4px 0 30px;
    width: 100%;
  }
}

.hero__bottom-title {
  font-size: 2.5vmin;
  line-height: 3.2vmin;
  letter-spacing: 0.4px;
  @include mobile {
    font-size: 7.5vmin;
    line-height: 9.7vmin;
  }
}

.hero__bottom-subtitle {
  margin-top: 15px;
  @include mobile {
    margin-top: 40px;
    margin-bottom: 27px;
  }
}

.hero__bottom-desc {
  @include flex-v-center;
  padding: 9px 0;
  line-height: 2.1vmin;
  letter-spacing: 1.6px;
  &:not(:last-child) {
    border-bottom: 2px solid var(--color-greyLight);
  }
  @include mobile {
    display: block;
    font-size: 5vmin;
    line-height: 9.1vmin;
    // flex-direction: column;
    // align-items: flex-start;
    // gap: 1.7rem;
    padding: 0;
    margin-bottom: 2.5vmin;
    letter-spacing: 1px;
  }
}

.hero__bottom-desc--key {
  position: relative;
  flex-basis: 34.6%;
  color: var(--color-black);
  &::after {
    content: '';
    position: absolute;
    top: -8px;
    right: 0;
    bottom: -8px;
    width: 2px;
    background-color: var(--color-greyLight);
    @include mobile {
      display: none;
    }
  }
}

.hero__bottom-desc--value {
  font-weight: 600;
  padding-left: 10px;
  flex-basis: 55%;
  span {
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 0;
      right: 0;
      height: 2px;
      background-color: var(--color-red);

    }
  }
  @include mobile {
    padding-left: 0;
  }
}

.hero__bottom-subtext {
  margin-bottom: 5px;
  color: var(--color-blue);
  letter-spacing: 1.1px;
  @include mobile {
    font-size: 4.2vmin;
    line-height: 5.4vmin;
  }
}

