.header__container {
  position: relative;
  padding-top: 15px;
  padding-bottom: 25px;
  min-height: 579px;
  background: url('../img/bg-img1.png') 70% no-repeat;
  background-size: cover;
  @include mobile {
    min-height: 773px;
    background: url('../img/bg-img2.png') 0% 0% no-repeat;
  }
}

.header__content {
  width: 40.3%;
  @include mobile {
    width: 100%;
  }
}

.header__logo-image {
  width: 17vmin;
  @include mobile {
    width: 55vmin;
  }
}

.header__title {
  font-size: 4.3vmin;
  margin-top: 45px;;
  line-height: 4.3vmin;
  span {
    display: inline-block;
  }
  @include mobile {
    font-size: 10.4vmin;
    margin-top: 40px;
    line-height: 10vmin;
    letter-spacing: -.7px;
  }
}

.header__text {
  width: 90%;
  margin-top: 19px;
  letter-spacing: .4px;
  transform: translateX(17px);
  @include mobile {
    font-size: 4vmin;
    width: 100%;
    margin-top: 28px;
    text-align: left;
    transform: translateX(0px);
    letter-spacing: 2px;
    line-height: 6.2vmin;
  }
}

.header__desc {
  @include flex-all-center;
  align-items: stretch;
  gap: 16px;
  width: 96%;
  margin-top: 32px;
  .btn-support {
    background-color: var(--color-black);
    color: var(--color-white);
    transition: background-color var(--time), color var(--time), border var(--time);
    &:hover {
      color: var(--color-gray);
      background-color: var(--color-yellow);
      border-color: var(--color-yellow);
    }
  }
  .btn-more {
    color: var(--color-gray);
    transition: background-color var(--time), color var(--time);
    &:hover {
      color: var(--color-white);
      background-color: var(--color-gray);
    }
  }
  @include mobile {
    margin-top: 33px;
    flex-direction: column;
    gap: 14px;
  }
}

.header__desc-btn {
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  border: 1px solid var(--color-black);
  border-radius: 66px;
  padding: 12px 3vmin;
  flex-basis: 50%;
  @include mobile {
    padding: 16px 4vmin;
    letter-spacing: 1.8px;
  }
}
