.players__container {
  position: relative;
  margin-bottom: 112px;
}

.players__container-title {
  @include flex-all-sb;
  margin-bottom: 50px;
  width: 88.2%;
  @include mobile {
    margin-bottom: 25px;
  }
}

.players__title-text {
  font-size: 3.8vmin;
  @include mobile {
    font-size: 7.5vmin;
  }
}

.players__title-options {
  @include flex-v-center;
  position: relative;
  min-width: 9.5rem;
  @include mobile {
    position: absolute;
    bottom: -15vmin;
    left: 46%;
    transform: translateX(-50%);
  }
}

.players__pagination {
  bottom: auto;
  pointer-events: none;
  @include mobile {
    bottom: -4px;
  }
}

.players__btn-next {
  @include mobile {
    right: 22px !important;
  }
}

.players__btn-prev {
  @include mobile {
    left: 22px !important;
  }
}

.players__swiper {
  width: 91.5%;
  transform: translateX(-4vmin);
}

.players__slide-content {
  @include flex-v-center;
  flex-direction: column;
  gap: 1rem;
}

.players__content-img {
  height: 20.5vmin;
  margin-bottom: 10px;
  img {
    height: 100%;
  }
  @include mobile {
    height: 50vmin;
  }
}

.players__content-name {
  font-weight: 600;
  font-size: 1.6vmin;
  margin-bottom: -10px;
  letter-spacing: 1.5px;
  @include small-tablet {
    font-size: 1.9vmin;
  }
  @include mobile {
    font-size: 5.5vmin;
  }
}

.players__content-status {
  letter-spacing: 1.1px;
  @include mobile {
    font-size: 4.1vmin;
  }
}

.players__content-btn {
  padding: 5px 10px;
  border: 1px solid var(--color-blue);
  border-radius: 20px;
  color: var(--color-blue);
  transition: background-color var(--time) color var(--time);
  &:hover {
    color: var(--color-white);
    background-color: var(--color-blue);
  }
  @include mobile {
    padding: 3px 10px;
  }
}


