// для использования sass-переменные - удалить root
// colors
:root {
  // base
  --font-familyG: "GolosText", sans-serif;
  --font-familyM: "Merriweather", serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --time: .3s;

  // colors
  --color-white: #fff;
  --color-black: #313131;
  --color-gray: #1f1f1f;
  --color-greyLight: #d0d0d0;
  --color-yellow: #fbce51;
  --color-red: #f54932;
  --color-blue: #3057a2;
  --color-beige: #e9ded4;
}
