html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-familyM, sans-serif);
  -webkit-text-size-adjust: 100%;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  color: var(--color-black);
}

b {
  color: var(--color-red);
}

.site-container {
  overflow: hidden; // при использовании position: sticky - убрать
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.title {
  font-weight: 400;
  text-transform: uppercase;
}

.text {
  font-family: var(--font-familyG), sans-serif;
  font-weight: 400;
  font-size: 1.2vmin;
  line-height: 20px;
  @include small-tablet {
    font-size: 1.5vmin;
    line-height: 15px;
  }
  @include mobile {
    font-size: 3.1vmin;
  }
}

.textMin {
  font-family: var(--font-familyG), sans-serif;
  font-weight: 400;
  font-size: 1.1vmin;
  line-height: 20px;
  @include small-tablet {
    font-size: 1.4vmin;
    line-height: 15px;
  }
  @include mobile {
    font-size: 3vmin;
  }
}

.swiper__btn {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background-color: var(--color-black);
  transition: background-color var(--time);
  &::after {
    content: '';
    width: 13px;
    height: 13px;
    border-left: 3px solid var(--color-white);
    border-bottom: 3px solid var(--color-white);
    transform: translateX(3px) rotate(45deg);
    @include mobile {
      width: 10px;
      height: 10px;
      transform: translateX(1px) rotate(45deg);
    }
  }
  &:nth-child(1) {
    &::after {
      transform: translateX(-3px) rotate(-135deg);
      @include mobile {
        transform: translateX(-1px) rotate(-135deg);
      }
    }
  }
  &:hover {
    background-color: var(--color-yellow);
  }
  @include mobile {
    width: 33px;
    height: 33px;
  }
}
